  font-family: 'mediumFont';
}

.descSubTitleContainer {
  font-size: 30px;
  font-family: 'lightFont';
}
@media screen and (min-width: 800px ) {
  .contact {
    padding-left: 20px;
    font-size: 30px;
  }
  .contactDetails {
    padding: 20px;
    font-size: 25px;
  }
  .portfolio {
    flex-direction: row;
    gap: 20px;
    flex-wrap: wrap;
  }
  .portfolioTitle {
    margin-left: 2rem;
  }
  .directionsContainer {
    font-size: 30px;
    
    
  }

  .descContainer {
